.Product-card {
  width: 300px;
  height: 500px;
  margin: 0 auto;
  position: relative;
  background-color: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-bottom: 1em;
}

.Product-card header {
  width: 300px;
  height: 300px;
  /* padding: 1em; */
}

.Product-card header img {
  width: 300px;
  height: 300px;
  padding: 2em;
}

.Product-card header h3 {
  width: 80%;
  position: absolute;
  font-size: 0.875rem;
  display: flex;
  justify-content: space-between;
  padding: 1em;
  line-height: 1.2rem;
}

.Product-card header h4 {
  cursor: pointer;
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 300px;
  height: 300px;
}

.Product-card header h4 span {
  color: #fff;
  font-size: 0.875rem;
  display: block;
  background-color: #000;
  margin-bottom: 0.5em;
  height: 36px;
  padding: 0.8em;
  margin: auto;
}

.Product-card > h3 {
  position: absolute;
  left: 1em;
  bottom: 11.5em;
}

.Product-card figure {
  cursor: pointer;
  display: flex;
  position: absolute;
  width: 38px;
  height: 38px;
  right: 2em;
  bottom: 11.35em;
  background-color: red;
  border-radius: 50%;
  z-index: 1;
}

.Product-card figure img {
  height: 14px;
  margin: auto;
}

.Product-card footer {
  position: relative;
  height: 200px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1em 1em;
}

.Product-card footer button {
  width: 260px;
  position: absolute;
  bottom: 1.5em;
  color: #fff;
  /* width: 100%; */
  padding: 1em;
  background-color: #000;
}

.Product-card footer p {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.875rem;
  margin: 1em 0;
  line-height: 1.2rem;
}

@media (min-width: 30em) {
  .Product-card {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .Product-card {
    margin-bottom: 3em;
  }
}

@media (min-width: 50em) {
  .Product-card {
    width: 24%;
    margin-bottom: 0.8em;
  }
}
