.Shipping-information {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 84%;
  margin: 0 auto 1em;
  background-color: #fff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
}

.Shipping-information div {
  display: flex;
  width: 100%;
  padding: 1em;
}

.Shipping-information img {
  width: auto;
  height: 42px;
  margin: auto 0;
}

.Shipping-information ul {
  display: flex;
  flex-direction: column;
  margin: auto 1em;
}

.Shipping-information ul li {
  width: 1005;
}

.Shipping-information ul li:first-of-type {
  font-weight: 600;
  text-transform: uppercase;
}

.Shipping-information ul li:last-of-type {
  font-size: 0.875rem;
  margin-top: 0.3em;
}

@media (min-width: 30em) {
  .Shipping-information {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .Shipping-information div {
    width: 50%;
  }
}

@media (min-width: 50em) {
  .Shipping-information div {
    width: 25%;
    padding: 2em;
  }
}
