.App .Shop-page {
  height: auto;
}
.Shop-page > header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 545x;
}

.Shop-page h2 {
  width: 100%;
  text-align: center;
  font-size: 2.4rem;
  margin: 1em 0;
}

.Shop-page section:first-of-type {
  width: 100%;
  margin: 0 0 3em;
}

.Shop-page section > header {
  display: flex;
  justify-content: space-between;
  width: 280px;
  margin: 0 auto;
}

.Shop-page section > header button {
  text-align: left;
  font-weight: 600;
  width: 48%;
  height: 42px;
  padding: 0 1.4em;
  border-radius: 35px;
  background-color: transparent;
  border: 2px solid #000;
}

.Shop-page section > header button:last-of-type {
  display: flex;
  justify-content: space-between;
  color: #fff;
  background-color: #000;
}
.Shop-page section > header button span {
  text-align: center;
  display: inline-block;
  margin: auto 0;
}

.Shop-page section > header button span:last-of-type {
  color: #000;
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  padding: 0.3em;
}

.Shop-page section form select {
}

.Shop-page > footer {
  width: 100%;
  text-align: center;
  margin-bottom: 3em;
}

.Shop-page > footer > button {
  font-size: 1rem;
  width: 180px;
  text-align: center;
  margin: 0 auto;
  padding: 1em;
  background-color: transparent;
}

@media (min-width: 30em) {
  .Shop-page section {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    margin: 3em auto;
  }
}
