.App-banner {
  width: 86%;
  margin: 3em 0;
  margin: 0 auto;
}

.App-banner .categories {
  width: 100%;
  height: auto;
  margin: auto 0;
  /* background-color: black; */
}

.App-banner .categories ul {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
}

.App-banner .categories ul li {
  position: relative;
  width: 100%;
  /* height: 10%; */
}

.App-banner .categories ul li a {
  font-size: 0.875rem;
  color: #fff;
  display: flex;
  width: 100%;
  padding: 1em;
  background-color: #000;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.App-banner .categories ul li img {
  width: 18px;
  height: 18px;
  margin-right: 1em;
}

.App-banner .categories > ul > li > a:hover {
  transition: ease-in 2s;
  opacity: 0.5;
}

.App-banner .categories ul li span {
  width: 80%;
  display: inline-block;
  margin: auto 0;
}

.App-banner .categories ul li:first-of-type {
  display: inline-block;
}

.App-banner .categories > ul > li:first-of-type {
  position: relative;
  font-size: 0.875rem;
  color: #fff;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 1em;
  background-color: red;
  z-index: 2;
}

.App-banner .categories > ul > li:first-of-type span {
  width: 85%;
}

.App-banner .categories ul ul {
  width: 100%;
  height: auto;
  color: black;
  background-color: #fff;
  /* box-shadow: 0 0 3px rgba(0, 0, 0, 0.1); */
}

.App-banner .categories ul ul a {
  display: inline-block;
  height: 100%;
  /* color: black; */
  background-color: rgba(0, 0, 0, 0.9);
  /* padding: 1.4em; */
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.App-banner .categories ul ul li a:hover {
  color: rgba(255, 255, 255, 0.5);
  transition: ease-in 1s;
  /* opacity: 0.5; */
}

.App-banner .slider .App-banner .categories .subcategories div li {
  display: none;
}

.App-banner .subcategories {
  display: none;
}

.App-banner .slider {
  position: relative;
  width: 100%;
  height: 500px;
  margin: 1em 0;
  background-color: seashell;
}

.App-banner .slider > div {
  display: flex;
  justify-content: space-between;
}

.App-banner .slider div div {
  width: 95%;
  color: #fff;
  position: absolute;
  padding: 1em;
  bottom: 1em;
}

.App-banner .slider div div h2 {
  font-size: 2.8rem;
  font-weight: 300;
  line-height: 3rem;
}

.App-banner .slider div div p {
  margin: 1em 0;
  line-height: 1.5rem;
}

.App-banner .slider div div button {
  color: #fff;
  padding: 1em 1.8em;
  border-radius: 35px;
  background-color: transparent;
  border: 2px solid #fff;
}

.App-banner .slider figure {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
}

.App-banner .slider img {
  width: auto;
  height: 100%;
}

.App-banner .slider footer {
  position: absolute;
  display: flex;
  justify-content: space-between;
  bottom: 2em;
  right: 2em;
  width: 60px;
}

.App-banner .slider footer button {
  width: 24px;
  height: 32px;
  background-color: red;
  border: 1px solid red;
}

.App-banner .slider footer button img {
  height: 14px;
  margin: auto;
}

@media (min-width: 30em) {
  .App-banner .categories ul li a {
    font-size: 1rem;
  }

  .App-banner .slider div div {
    width: 75%;
    padding: 3em;
  }

  .App-banner .slider footer {
    width: 90px;
  }

  .App-banner .slider footer button {
    width: 32px;
    height: 42px;
  }
}

@media (min-width: 50em) {
  .App-banner {
    display: flex;
    justify-content: space-between;
    margin: 1em auto;
  }

  .App-banner .categories {
    width: 450px;
    margin: 0;
    margin-right: 1em;
  }

  .animate__slideInDown {
    animation-name: none !important;
  }

  .App-banner .subcategories {
    display: inline-block;
  }

  .App-banner .slider {
    margin: 0;
    height: 505px;
  }

  .App-banner .slider > div {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
  }

  .App-banner .slider img {
    width: 100%;
    height: 100%;
  }

  .App-banner .slider footer {
    width: 100%;
    height: 60px;
    margin: auto 0;
    bottom: 14.5em;
    right: 0;
  }

  .App-banner .slider footer button {
    width: 36px;
    height: 56px;
  }

  .App-banner .slider div div {
    width: 75%;
    margin: auto;
    padding: 14% 0;
  }
}
