.Product-gallery {
  width: 86%;
  margin: 0 auto;
}

.Product-gallery > h2 {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  text-align: center;
  font-size: 1.4rem;
  margin: 1em 0;
  padding: 0.8em;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 30px 0;
}

.Product-gallery > h2 span {
  display: inline-block;
}

.Product-gallery > h2 span:last-of-type {
  cursor: pointer;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0.875rem;
  padding: 1.5em 0.8em;
  background-color: rgba(0, 0, 0, 1);
  border-radius: 0 0 25px 25px;
}

.Product-gallery > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Product-gallery > div > div:first-of-type {
  width: 100%;
  margin-bottom: 0.6em;
}

.Product-gallery > div > div:last-of-type {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.Product-gallery > div > div:last-of-type div {
  width: 48%;
  height: 48%;
  margin-bottom: 0.7em;
}

.Product-gallery > div div {
  position: relative;
}

.Product-gallery > div div h2 {
  font-weight: 500;
  font-size: 0.875rem;
  text-align: center;
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.Product-gallery > div div h2:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.Product-gallery > div div h2 span {
  color: #fff;
  display: block;
  width: 100%;
  height: 45px;
  margin: auto;
  padding: 1em 0.8em;
  background-color: #000;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
}

@media (min-width: 30em) {
  .Product-gallery > h2 {
    width: 100%;
  }

  .Product-gallery > h2 span:last-of-type {
    padding: 1.5em 2em;
  }
}

@media (min-width: 50em) {
  .Product-gallery > div {
    flex-direction: row;
    justify-content: space-between;
  }

  .Product-gallery > div > div:first-of-type {
    width: 49%;
  }

  .Product-gallery > div > div:last-of-type {
    width: 49%;
  }

  .Product-gallery > h2 span:last-of-type {
    padding: 1.5em 3em;
  }
}
