.App-footer {
  color: #fff;
  background-color: #000;
}

.App-footer > div:first-of-type {
  color: #000;
  background-color: #fff;
  padding: 1.5em;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.App-footer > div:first-of-type p {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  margin: 1em 0;
  line-height: 1.25rem;
}

.App-footer > div:first-of-type ul {
  display: flex;
  flex-wrap: wrap;
  margin: 2em 0;
}

.App-footer > div:first-of-type img {
  height: 20px;
  margin-right: 1em;
  margin-bottom: 1em;
}

.App-footer > div:last-of-type {
  padding: 1.5em;
}

.App-footer form {
  display: flex;
  justify-content: space-between;
  border-radius: 35px;
  border: 1px solid #fff;
  margin-bottom: 2em;
}
.App-footer form input {
  width: 45%;
  padding: 1em;
  border: none;
  border-radius: 35px;
}

.App-footer form input:first-of-type {
  width: 55%;
  background-color: transparent;
}

.App-footer form input:last-of-type {
  color: #000;
  margin-left: -3em;
  background-color: #fff;
}

.App-footer > div:last-of-type > div {
  padding: 1em 0;
}

.App-footer > div:last-of-type > div > div {
  margin-bottom: 2em;
}

.App-footer > div:last-of-type > div > div h4 {
  margin-bottom: 1em;
}

.App-footer > div:last-of-type > div > div li {
  margin: 0.5em 0;
}

.App-footer > div:last-of-type > div > div li a {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.5);
}

.App-footer > div:last-of-type > div:last-of-type {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.App-footer > div:last-of-type > div:last-of-type h4 {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
}

.App-footer > div:last-of-type > div:last-of-type ul {
  width: 100px;
  display: flex;
  justify-content: space-between;
  margin: auto 0;
}

.App-footer > div:last-of-type > div:last-of-type ul a {
  color: #fff;
}

.App-footer > div:last-of-type > div:last-of-type ul img {
  height: 16px;
}

@media (min-width: 30em) {
  .App-footer > div:first-of-type {
    display: flex;
    padding: 3em;
  }

  .App-footer > div:first-of-type div {
    width: 60%;
  }

  .App-footer > div:first-of-type p {
    font-size: 1rem;
    width: 80%;
    margin: 2em 0;
  }

  .App-footer > div:first-of-type ul {
    width: 50%;
  }

  .App-footer > div:first-of-type img {
    height: 14px;
  }

  .App-footer > div:last-of-type {
    padding: 3em;
  }

  .App-footer > div:last-of-type > div:nth-child(2) {
    width: 80%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }
}

@media (min-width: 50em) {
  .App-footer {
    display: flex;
  }

  .App-footer > div:first-of-type {
    width: 560px;
    flex-direction: column;
    padding: 3em 3em 0;
  }

  .App-footer > div:first-of-type ul {
    width: 100%;
  }

  .App-footer > div:first-of-type div {
    width: 100%;
  }

  .App-footer form {
    width: 600px;
    margin: 0 auto 2em;
  }

  .App-footer > div:last-of-type {
    width: 65%;
    padding: 3em 3em 1em;
  }

  .App-footer > div:last-of-type > div:last-of-type {
    width: 600px;
    margin: 0 auto;
  }
}
