.Onsell-products {
  width: 91%;
  margin: 0 auto;
}

.Onsell-products h2 {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  text-align: center;
  font-size: 1.4rem;
  margin: 1em 0;
  padding: 0.8em;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 30px 0;
}

.Onsell-products h2 span {
  display: inline-block;
}

.Onsell-products h2 span:last-of-type {
  cursor: pointer;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0.875rem;
  padding: 1.5em 0.8em;
  background-color: rgba(0, 0, 0, 1);
  border-radius: 0 0 25px 25px;
}

.Onsell-products .Card {
  width: 300px;
  position: relative;
  background-color: #fff;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-bottom: 1em;
}

.Onsell-products .Card header {
  width: 300px;
  height: 300px;
  padding: 1em;
}

.Onsell-products .Card header h3 {
  position: absolute;
  font-size: 0.875rem;
  display: flex;
  justify-content: space-between;
}

.Onsell-products .Card header h4 {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 300px;
  height: 300px;
}

.Onsell-products .Card header h4 span {
  color: #fff;
  font-size: 0.875rem;
  display: block;
  background-color: #000;
  margin-bottom: 0.5em;
  height: 36px;
  padding: 0.8em;
  margin: auto;
}

.Onsell-products .Card figure {
  display: flex;
  position: absolute;
  width: 38px;
  height: 38px;
  right: 2em;
  margin-top: -1.2em;
  background-color: red;
  border-radius: 50%;
  z-index: 1;
}

.Onsell-products .Card figure img {
  height: 14px;
  margin: auto;
}

.Onsell-products .Card footer {
  position: relative;
  height: 200px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding: 1em 1em;
}

.Onsell-products .Card footer button {
  color: #fff;
  width: 100%;
  padding: 1em;
  background-color: #000;
}

.Onsell-products .Card footer p {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.875rem;
  margin: 1em 0;
  line-height: 1.2rem;
}

@media (min-width: 30em) {
  .Onsell-products {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 2em;
  }

  .Onsell-products h2 {
    width: 100%;
  }

  .Onsell-products h2 span:last-of-type {
    padding: 1.5em 2em;
  }

  .Onsell-products .Card {
    margin-bottom: 3em;
  }
}

@media (min-width: 50em) {
  .Onsell-products h2 span:last-of-type {
    padding: 1.5em 3em;
  }
}
