.Single-product {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  /* padding: 1.5em; */
  width: 100vw;
  height: 100vh;
  z-index: 5;
}

.Single-product > div {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.Single-product article {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 96vh;
  margin: auto;
  z-index: 6;
}

.Single-product article > div:first-of-type {
  color: #fff;
  background-color: #000;
  padding: 1em;
}

.Single-product article > div:first-of-type h2 {
  display: flex;
  justify-content: space-between;
  font-size: 1.25em;
  margin-top: 0;
  margin-bottom: 0.2em;
}

.Single-product article > div:first-of-type h2 img {
  height: 14px;
}

.Single-product article > div:first-of-type > h4 {
  font-size: 0.875rem;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 1.4em;
}

.Single-product article > div:first-of-type > h3 {
  margin-bottom: 1em;
}

.Single-product article > div:first-of-type > div:first-of-type ul {
  color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-between;
}

.Single-product article > div:first-of-type > div:first-of-type ul li {
  width: 50%;
  font-size: 0.875rem;
  padding-bottom: 0.5em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.Single-product
  article
  > div:first-of-type
  > div:first-of-type
  ul
  li:first-of-type {
  color: #fff;
  border-bottom: 2px solid #ffff;
}

.Single-product article > div:first-of-type > div:first-of-type p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  line-height: 1rem;
  margin: 0.5em 0;
}

.Single-product article > div:first-of-type > div:last-of-type {
  display: flex;
  justify-content: space-between;
  margin-top: 0.8em;
  margin-bottom: 0.8em;
}

.Single-product article > div:first-of-type > div:last-of-type div ul {
  display: flex;
}

.Single-product
  article
  > div:first-of-type
  > div:last-of-type
  > div:first-of-type
  > ul {
  height: 50px;
}

.Single-product
  article
  > div:first-of-type
  > div:last-of-type
  div:first-of-type
  ul
  li {
  width: 18px;
  height: 18px;
  margin: auto 0.5em;
  margin-left: 0;
  background-color: #fff;
  border-radius: 50%;
}

.Single-product article > div:first-of-type div:last-of-type > ul {
  border: 1px solid rgba(255, 255, 255, 0.5);
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  border-radius: 25px;
}

.Single-product article > div:first-of-type div:last-of-type > ul li {
  padding: 0.5em 0.8em;
}

.Single-product article > div:first-of-type div:last-of-type li:nth-child(2) {
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}

.Single-product article > div:first-of-type > h4:last-of-type {
  float: left;
  flex-direction: column;
  margin-top: 0.5em;
  margin-bottom: 0;
}

.Single-product article > div:first-of-type > h4:last-of-type span {
  display: block;
  margin-bottom: 0.5em;
}

.Single-product article > div:first-of-type > h4 span:first-of-type {
  text-transform: uppercase;
  color: #fff;
}

.Single-product article > div:first-of-type button {
  text-transform: uppercase;
  color: #fff;
  float: right;
  width: 140px;
  height: 42px;
  border-radius: 35px;
  border: 2px solid rgba(255, 255, 255, 0.25);
  background-color: transparent;
}

.Single-product article > div:last-of-type {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 300px;
  overflow: hidden;
}

.Single-product article > div:last-of-type figure {
  display: flex;
  width: 100%;
  height: 100%;
  /* padding: 1em; */
  background-color: #fff;
}

.Single-product article > div:last-of-type figure img {
  width: 220px;
  height: 220px;
  /* padding: 1em; */
  margin: auto;
  /* margin-top: -20px; */
}

.Single-product article > div:last-of-type ul {
  position: absolute;
  bottom: 0;
  display: flex;
  width: 110px;
  margin: 1em 6em;
  text-align: center;
}

.Single-product article > div:last-of-type ul li {
  width: 15px;
  height: 15px;
  margin: 0.5em;
  margin-left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}

@media (min-width: 30em) {
  .Single-product article {
    width: 500px;
  }

  .Single-product article > div:first-of-type {
    /* width: 300px; */
    padding: 3em;
  }

  .Single-product article > div:first-of-type > h3 {
    margin: 2em 0;
  }

  .Single-product article > div:last-of-type {
    width: 500px;
    height: 500px;
  }

  .Single-product article > div:first-of-type > div:first-of-type p {
    font-size: 1rem;
    line-height: 1.5rem;
    margin: 1em 0;
  }

  .Single-product article > div > div:first-of-type {
    margin: 2em 0;
  }

  .Single-product article > div:last-of-type figure img {
    width: 450px;
    height: 450px;
  }

  .Single-product article > div:last-of-type ul {
    width: 200px;
    margin: 3em 11em;
  }

  .Single-product article > div:last-of-type ul li {
    width: 24px;
    height: 24px;
  }
}

@media (min-width: 50em) {
  .Single-product article {
    width: 1000px;
    height: 600px;
    flex-direction: row;
  }

  .Single-product article > div:first-of-type {
    width: 400px;
  }

  .Single-product article > div h4 {
    margin-bottom: 1.5em;
  }

  .Single-product
    article
    > div:first-of-type
    > div:last-of-type
    > div:first-of-type
    > ul {
    height: 37px;
  }

  .Single-product article > div:first-of-type {
    padding: 2em;
  }

  .Single-product article > div:first-of-type > h4:last-of-type {
    float: none;
    font-size: 1rem;
  }

  .Single-product article > div:first-of-type button {
    font-size: 1.25rem;
    float: none;
    width: 100%;
    height: 64px;
    margin-top: 1.5em;
  }

  .Single-product article > div:last-of-type {
    width: 600px;
    height: 600px;
  }

  .Single-product article > div:last-of-type ul {
    margin: 3em 15em;
  }
}
